* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #fafafa;
  color: #333;
}

.full {
  width: 100%;
  min-height: 100vh
}

.full20 {
  width: 100%;
  min-height: 20vh
}

.full40 {
  width: 100%;
  min-height: 40vh
}

.full60 {
  width: 100%;
  min-height: 60vh
}

.blackMask06 {
  background-color: rgba(0, 0, 0, 0.6);
}

.tomatoMask06 {
  background-color: rgba(255, 99, 71, 0.6);
  border-radius: 5px
}

.center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.centerRow {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.mainTitle {
  font-family: 'Kalam';
}

.mainTitle h1 {
  text-transform: uppercase;
  font-size: 60px;
  font-weight: 700;
  margin-bottom: 30px;
}

.title {
  font-family: "Lucida Console", Monaco, monospace;
  font-size: 2em
}

.margin10 {
  margin: 10px;
}

.padding5 {
  padding: 5px;
}

.padding10 {
  padding: 10px;
}

.padding20 {
  padding: 20px;
}

.padding30 {
  padding: 30px;
}

.radius100 {
  border-radius: 100%;
}

.padding50Side {
  padding-left: 50px;
  padding-right: 50px;
}

.opacitybk03 {
  /* background-color: rgba(44, 168, 255,0.3);  */
  background-color: rgba(44, 168, 255,0.1); 
  border: none;
}

.mainColorBk {
  background-color: #2CA8FF;
}

.blackbackground {
  background-color: #000;
}

.whiteFont {
  color: #fff
}

.delay {
    transition: all 0.5s ease;
}

.delay2 {
    transition: all 0.7s ease;
}

.delay3 {
  transition: all 1s ease;
}

.boxShadow {
  box-shadow:0 10px 16px 0 rgba(0,0,0,0.2)
}

.boxShadow:hover {
  box-shadow:0 15px 21px 0 rgba(0,0,0,0.2)
}

.boxShadowHover:hover {
  box-shadow:0 10px 16px 0 rgba(0,0,0,0.2);
  border-radius: 10px;
}

.errorBk {
  background-color: #A2250A;
  border: none;
}

.successBk {
  background-color: #04B45F;
  border: none;
}

.opacityBK {
  border: solid #fff 2px;
  border-radius: 10px;
  width: 250px;
  height: 50px;
  margin-right: 50px;
  margin-bottom: 100px;
}

span {
  color: #2CA8FF;
}